import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { NbJSThemeOptions } from '@nebular/theme/services/js-themes/theme.options';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'bk-pie-chart',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    ':host { display: block }',
  ],
  template: `
    <div echarts [options]="options" [autoResize]="true" [style.height.%]="100"></div>
  `,
})
export class BkPieChartComponent implements OnInit, OnDestroy {

  @Input() set data(data) {
    this._data = data;
    this.updateOptions();
  }

  options: any = {};

  private _data = [];
  private colorConfig: any = {};
  private destroyed$: Subject<void> = new Subject();

  constructor(private theme: NbThemeService) {
  }

  ngOnInit() {
    this.theme.getJsTheme()
      .pipe(takeUntil(this.destroyed$))
      .subscribe((config: NbJSThemeOptions) => {
        this.colorConfig = config.variables.charts;
        this.updateOptions();
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
  }

  private updateOptions() {
    this.options = {
      backgroundColor: this.colorConfig.bg,
      color: [
        this.colorConfig.warning,
        this.colorConfig.info,
        this.colorConfig.danger,
        this.colorConfig.success,
        this.colorConfig.primary,
      ],
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        left: 'left',
        data: ['USA', 'Germany', 'France', 'Canada', 'Russia'],
        textStyle: {
          color: this.colorConfig.textColor,
        },
      },
      series: [
        {
          name: 'Countries',
          type: 'pie',
          radius: '80%',
          center: ['50%', '50%'],
          data: this._data,
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: this.colorConfig.itemHoverShadowColor,
            },
          },
          label: {
            normal: {
              textStyle: {
                color: this.colorConfig.textColor,
              },
            },
          },
          labelLine: {
            normal: {
              lineStyle: {
                color: this.colorConfig.axisLineColor,
              },
            },
          },
        },
      ],
    };
  }

}
