import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { NbJSThemeOptions } from '@nebular/theme/services/js-themes/theme.options';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'bk-multiple-axis-chart',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    ':host { display: block }',
  ],
  template: `
    <div echarts [options]="options" [autoResize]="true" [style.height.%]="100"></div>
  `,
})
export class BkMultipleAxisChartComponent implements OnInit, OnDestroy {

  @Input() set data(data: { firstDataset: [], secondDataset: [] }) {
    this._data = data;
    this.updateOptions();
  }

  options: any = {};

  private _data: { firstDataset: [], secondDataset: [] } = {firstDataset: [], secondDataset: [] };
  private colorConfig: any = {};
  private destroyed$: Subject<void> = new Subject();

  constructor(private theme: NbThemeService) {
  }

  ngOnInit() {
    this.theme.getJsTheme()
      .pipe(takeUntil(this.destroyed$))
      .subscribe((config: NbJSThemeOptions) => {
        this.colorConfig = config.variables.charts;
        this.updateOptions();
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
  }

  private updateOptions() {
    this.options = {
      backgroundColor: this.colorConfig.bg,
      color: [
        this.colorConfig.primary,
        this.colorConfig.info,
      ],
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
        },
      },
      legend: {
        data: ['First Dataset', 'Second Dataset'],
        textStyle: {
          color: this.colorConfig.textColor,
        },
      },
      xAxis: [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true,
          },
          data: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          axisLine: {
            lineStyle: {
              color: this.colorConfig.axisLineColor,
            },
          },
          splitLine: {
            lineStyle: {
              color: this.colorConfig.splitLineColor,
            },
          },
          axisLabel: {
            textStyle: {
              color: this.colorConfig.textColor,
            },
          },
        },
      ],
      yAxis: [
        {
          type: 'value',
          name: 'First Dataset',
          min: -90,
          max: 60,
          position: 'left',
          axisLine: {
            lineStyle: {
              color: this.colorConfig.primary,
            },
          },
          axisLabel: {
            formatter: '{value} Â°C',
          },
        },
        {
          type: 'value',
          name: 'Second Dataset',
          min: -90,
          max: 80,
          position: 'right',
          axisLine: {
            lineStyle: {
              color: this.colorConfig.danger,
            },
          },
          axisLabel: {
            formatter: '{value} Â°C',
          },
          splitLine: {
            show: false,
          },
        },

      ],
      series: [
        {
          name: 'First Dataset',
          type: 'line',
          yAxisIndex: 0,
          smooth: true,
          itemStyle: {color: this.colorConfig.primary},
          data: this._data.firstDataset,
        },
        {
          name: 'Second Dataset',
          type: 'line',
          yAxisIndex: 1,
          smooth: true,
          itemStyle: {color: this.colorConfig.danger},
          data: this._data.secondDataset,
        },
      ],
    };
  }

}
