import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { NbJSThemeOptions } from '@nebular/theme/services/js-themes/theme.options';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'bk-line-chart',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    ':host { display: block }',
  ],
  template: `
    <div echarts [options]="options" [autoResize]="true" [style.height.%]="100"></div>
  `,
})
export class BkLineChartComponent implements OnInit, OnDestroy {

  @Input() set data(data: { income: [], outcome: [], profit: [] }) {
    this._data = data;
    this.updateOptions();
  }

  options: any = {};

  private _data: { income: [], outcome: [], profit: [] } = { income: [], outcome: [], profit: [] };
  private colorConfig: any = {};
  private destroyed$: Subject<void> = new Subject();

  constructor(private theme: NbThemeService) {
  }

  ngOnInit() {
    this.theme.getJsTheme()
      .pipe(takeUntil(this.destroyed$))
      .subscribe((config: NbJSThemeOptions) => {
        this.colorConfig = config.variables.charts;
        this.updateOptions();
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
  }

  private updateOptions() {
    this.options = {
      backgroundColor: this.colorConfig.bg,
      color: [this.colorConfig.danger, this.colorConfig.primary, this.colorConfig.info],
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c}',
      },
      xAxis: [
        {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisTick: {
            alignWithLabel: true,
          },
          axisLine: {
            lineStyle: {
              color: this.colorConfig.axisLineColor,
            },
          },
          axisLabel: {
            textStyle: {
              color: this.colorConfig.textColor,
            },
          },
        },
      ],
      yAxis: [
        {
          axisLine: {
            lineStyle: {
              color: this.colorConfig.axisLineColor,
            },
          },
          splitLine: {
            lineStyle: {
              color: this.colorConfig.splitLineColor,
            },
          },
          axisLabel: {
            textStyle: {
              color: this.colorConfig.textColor,
            },
          },
        },
      ],
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true,
      },
      series: [
        {
          name: 'Income',
          type: 'line',
          data: this._data.income,
        },
        {
          name: 'Outcome',
          type: 'line',
          data: this._data.outcome,
        },
        {
          name: 'Profit',
          type: 'line',
          data: this._data.profit,
        },
      ],
    };
  }

}
