import { HttpClient } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { NbJSThemeOptions } from '@nebular/theme/services/js-themes/theme.options';
import { registerMap } from 'echarts';
import { combineLatest, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'bk-bubble-map',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    ':host { display: block }',
  ],
  template: `
    <div echarts [options]="options" [autoResize]="true" [style.height.%]="100"></div>
  `,
})
export class BkBubbleMapComponent implements OnInit, OnDestroy {

  @Input() set data(data: { countryCoordinates: {}, mapData: any[] }) {
    this._data = data;

    // avoid showing map without colors
    if (this.initialized) {
      this.updateOptions();
    }
  }

  options: any = {};

  private _data: { countryCoordinates: {}, mapData: any[] } = { countryCoordinates: [], mapData: [] };

  private colorConfig: any = {};
  private geoColors: any[] = [];
  private countryColorMap: any = {};

  private max = -Infinity;

  private initialized: boolean;

  private destroyed$: Subject<void> = new Subject();

  constructor(private theme: NbThemeService, private http: HttpClient, private cd: ChangeDetectorRef) {
  }

  ngOnInit() {
    combineLatest([
      this.http.get('assets/map/world.json'),
      this.theme.getJsTheme(),
    ])
      .pipe(takeUntil(this.destroyed$))
      .subscribe(([map, config]: [any, NbJSThemeOptions]) => {
        registerMap('world', map);
        this.initColors(config);
        this.updateOptions();

        this.initialized = true;
        this.cd.detectChanges();
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
  }

  private initColors(config: NbJSThemeOptions) {
    this.colorConfig = config.variables.bubbleMap;

    this.geoColors = [
      this.colorConfig.primary,
      this.colorConfig.info,
      this.colorConfig.success,
      this.colorConfig.warning,
      this.colorConfig.danger,
    ];

    this._data.mapData.forEach(itemOpt => {
      this.countryColorMap[itemOpt.name] = this.getRandomGeoColor();
    });
  }

  private updateOptions() {
    this._data.mapData.forEach((itemOpt) => {
      if (itemOpt.value > this.max) {
        this.max = itemOpt.value;
      }
    });

    this.options = {
      title: {
        text: 'World Population (2011)',
        left: 'center',
        top: 'top',
        textStyle: {
          color: this.colorConfig.titleColor,
        },
      },
      tooltip: {
        trigger: 'item',
        formatter: params => {
          return params.name + ': ' + params.value[2];
        },
      },
      visualMap: {
        show: false,
        min: 0,
        max: this.max,
        inRange: {
          symbolSize: [6, 60],
        },
      },
      geo: {
        name: 'World Population (2010)',
        type: 'map',
        map: 'world',
        roam: true,
        label: {
          emphasis: {
            show: false,
          },
        },
        itemStyle: {
          normal: {
            areaColor: this.colorConfig.areaColor,
            borderColor: this.colorConfig.areaBorderColor,
          },
          emphasis: {
            areaColor: this.colorConfig.areaHoverColor,
          },
        },
        zoom: 1.1,
      },
      series: [
        {
          type: 'scatter',
          coordinateSystem: 'geo',
          data: this._data.mapData.map(itemOpt => {
            return {
              name: itemOpt.name,
              value: [
                this._data.countryCoordinates[itemOpt.code].longitude,
                this._data.countryCoordinates[itemOpt.code].latitude,
                itemOpt.value,
              ],
              itemStyle: {
                normal: {
                  color: this.getCountryColor(itemOpt.name),
                },
              },
            };
          }),
        },
      ],
    };
  }

  private getCountryColor(countryName) {
    const color = this.countryColorMap[countryName];
    if (color) {
      return color;
    }

    const generatedColor = this.getRandomGeoColor();
    this.countryColorMap[countryName] = generatedColor;

    return generatedColor;
  }

  private getRandomGeoColor() {
    const index = Math.round(Math.random() * this.geoColors.length);
    return this.geoColors[index];
  }
}
